body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 450px) {
  .col-2 {
    display: none !important;
  }

  .rating {
    color: red;
  }
}

.App {
  text-align: center;
}

.row {
  margin-right: 0px !important;
}

.main-content .card-deck {
  display: flex;
  justify-content: flex-start;
  flex-flow: row wrap;
  align-items: stretch;
  padding: 50px 50px 10px 50px;
  margin-right: 0 !important;
  background-color: #616d1d;
  border-top: 13px solid #e6d0b3;
  border-left: 13px solid #e6d0b3;
}

.card-deck .card {
  display: block;
  flex-basis: 33.3%; /* change this value for each breakpoint*/
}

.card {
  margin-right: 1em !important;
  max-width: 300px !important;
  min-width: 300px !important;
  max-height: 480px;
}

h5.card-title {
  font-size: 1.4rem !important;
}

.card-text {
  font-size: 15px !important;
}

.card-img-top {
  max-height: 167px !important;
}

.card-footer {
  background-color: white !important;
}

.year {
  font-weight: 800;
  font-size: 0.9em;
}

small.year:hover {
  cursor: pointer;
}

.bufon {
  border: 2px solid#4f4b68;
  overflow: hidden;
  display: inline-block;
  padding: 2px 20px;
  border-radius: 10px;
  vertical-align: middle;
  color: #4f4a66;
}

.bufon-stars {
  border: 2px solid#616d1d;
  overflow: hidden;
  display: inline-block;
  padding: 2px 8px;
  background-color: #616d1d;
  float: right;
  border-radius: 4px;
  vertical-align: middle;
  min-width: 110px;
}

.bufon:hover {
  color: #ecdbc6;
  background-color: #4f4a66;
  cursor: pointer;
}

.bufon-stars:hover {
  border: 2px solid#485115;
  background-color: #485115;
  cursor: pointer;
}

.card-body {
  background-color: #2c4a52;
  background-color: #39364a;
  color: white;
  padding: 1.25rem;
  min-height: 200px;
}

.rating {
  float: left;
  color: #fccd69;
  font-size: 0.96em;
}

.rating:hover {
  cursor: pointer;
  color: #fcd682;
}

.main-content {
  display: block;
}

.list-group {
  margin-top: 2em;
  padding-left: 10px;
}

.col-2 {
  border-top: 13px solid #e6d0b3;
  background-color: #485115;
  padding: 10px !important;
  margin: 0 auto !important;
  margin-left: 10px !important;
}

.col {
  padding-right: 0px !important;
}

.pages {
  border-left: 13px solid #e6d0b3;
  padding: 1.25rem;
  margin-left: -15px;
  background-color: #616d1d;
  padding-left: 65px;
}

.page-link {
  color: black !important;
  background-color: #e6d0b3 !important;
  cursor: pointer;
}

.page-link:hover {
  color: #e6cfb3 !important;
  background-color: #656085 !important;
  cursor: pointer;
  border: 1px solid #656085 !important;
}

.page-item.active .page-link {
  background-color: #4f4b68 !important;
  color: #e6d0b3 !important;
  border-color: #4f4b68 !important;
}

a {
  color: black !important;
  font-weight: 600 !important;
}

a:active,
a:visited {
  color: #22202 !important;
  font-weight: 600 !important;
}

a.list-group-item:hover {
  color: #39364a !important;
  background-color: #dfc39f !important;
  font-weight: 600 !important;
}

a.list-group-item:focus {
  color: #39364a !important;
  background-color: #dfc39f !important;
  font-weight: 600 !important;
}

.list-group-item {
  background-color: #39364a !important;
  color: #e0cbaf !important;
  text-transform: capitalize !important;
  font-weight: 600 !important;
  cursor: pointer !important;
}

.list-group-item-selected {
  position: relative;
  display: block;
  padding: 0.75rem 1.25rem;
  background-color: #39364a !important;
  color: #9ddc56 !important;
  text-transform: capitalize !important;
  font-weight: 600 !important;
  cursor: pointer !important;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-bottom: 0px;
}

.list-group-item:hover {
  background-color: #39364a !important;
  color: #9ddc56 !important;
}

