.timeline {
  background-color: #ecdbc6;
  font-family: Courier, monospace, sans-serif;
  position: relative;
  margin: 0 auto;
}

.timeline::after {
  content: "";
  position: absolute;
  width: 5px;
  background-color: #616d1d;
  top: 70px;
  bottom: 50px;
  left: 50%;
  margin-left: -3px;
  z-index: 0;
}

.timeline-top-circle {
  padding-top: 40px;
  width: 50%;
  position: relative;
}

.timeline-top-circle::after {
  content: "";
  position: absolute;
  width:26px;
  height: 26px;
  right: -13px;
  background-color: #616d1d;
  border: 4px solid #616d1d;
  border-radius: 50%;
  top: 8;
  top: 112%;
  z-index: 4;
}

.timeline-bottom-circle {
  padding-bottom: 100px;
  width: 50%;
  position: relative;
}

.timeline-bottom-circle::after {
  content: "";
  position: absolute;
  width:26px;
  height: 26px;
  right: -13px;
  background-color: #616d1d;
  border: 4px solid #616d1d;
  border-radius: 50%;
  top: 49%;
  z-index: 4;
}

.timeline-container-left {
  padding: 0px 60px 0px 20px;
  position: relative;
  background-color: inherit;
  width: 50%;
}

.timeline-container-right {
  padding: 10px 20px 0px 60px;
  position: relative;
  background-color: inherit;
